import Vue from 'vue'
import VueRouter from 'vue-router'
import firebase from "firebase"
//import Chat from '../Chat.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/home',
        name: 'home',
        component: () => import('../views/Home'),
    },
    {
        path: '/dashboard',
        name: 'dashboard',
        component: () => import('../views/Dashboard/Layout'),
        children: [
            {
                path: 'indicators',
                name: 'dashboard.indicators',
                component: () => import('../views/Dashboard/Indicators')
            },
            {
                path: 'backup',
                name: 'dashboard.backup',
                component: () => import('../views/Dashboard/Backup')
            },
            {
                path: 'logs',
                name: 'dashboard.logs',
                component: () => import('../views/Dashboard/Logs')
            }
        ]
    },
    // {
    //     path: '/',
    //     name: 'login',
    //     component: () => import('../Login.vue'),
    //     meta: { requiresAuth: true }
    // },
    {
        path: '/chat',
        name: 'Chat',
        component: () => import('../Chat.vue'),
        // meta: { requiresAuth: true },
        props: true,
        // beforeEnter: (to,from,next) => {
        //     if(to.params.name){
        //         next();
        //     } else {
        //         next({name: 'login'})
        //     }
        // }
    },
    {
        path: '/login',
        name: 'login',
        component: () => import('../loginuser.vue'),
    },
    {
        path: '/register',
        name: 'register',
        component: () => import('../Register.vue'),
    },
    {
        path: '/',
        name: 'landing',
        component: () => import('../Landing.vue'),
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

router.beforeEach((to, from, next) => {
    const requiresAuth = to.matched.some(record => record.meta.requiresAuth)
    const isAuthenticated = firebase.auth().currentUser
    if (requiresAuth && !isAuthenticated) {
      next('/login')
    } else {
      next()
    }
  })

  export default router
